import React from 'react';
import { PseudoBox, Text, Heading, Stack, Box, Flex } from '@chakra-ui/core';
import { IoLogoGithub, IoMdLink } from 'react-icons/io';

export function LinkButton({ icon, link, size, ...props }) {
  return (
    <Box as='a' href={link} target='_blank' {...props}>
      <PseudoBox
        as={icon}
        size={size}
        color='white'
        transition='all 0.3s'
        _hover={{ color: 'green.500' }}
      />
    </Box>
  );
}

function ProjectCard({ tags, title, description, src, link, link2, ...props }) {
  return (
    <PseudoBox
      as='article'
      bg='gray.700'
      transition='all .3s'
      cursor='pointer'
      padding='40px'
      borderRadius='md'
      _hover={{ transform: 'scale(1.02)' }}
      {...props}
    >
      <Flex flexDirection={{ base: 'column', md: 'row' }}>
        <Box>
          <Heading size='lg'>{title}</Heading>

          <Text mt={2} fontSize='sm' color='gray.400'>
            {description}
          </Text>

          <Stack mt={6} spacing={6} isInline>
            {tags.map((tag) => (
              <Text
                fontSize='xs'
                letterSpacing='wide'
                textTransform='uppercase'
                fontWeight='bold'
              >
                {tag}
              </Text>
            ))}
          </Stack>

          <Stack mt={6} spacing={5} isInline>
            <LinkButton icon={IoLogoGithub} link={link} size='24px' />
            <LinkButton icon={IoMdLink} link={link2} size='24px' />
          </Stack>
        </Box>
        
        <Box
          as='img'
          objectFit='contain'
          src={src}
          height='200px'
          width='440px'
          paddingY={4}
        ></Box>
      </Flex>
    </PseudoBox>
  );
}

export default ProjectCard;
