import React from 'react';
import { Box, Stack, Divider, PseudoBox } from '@chakra-ui/core';
import { FaTwitter, FaLinkedin, FaGlobe, FaGithub } from 'react-icons/fa';

export function HoverLink({ link, label, ...props }) {
  return (
    <PseudoBox
      as="a"
      display="inline-block"
      href={link}
      target="__blank"
      rel="nofollow noopener noreferrer"
      aria-label={label}
      transition="all 0.3s"
      _hover={{ transform: `translateY(-4px)`, color: 'teal.500' }}
      {...props}
    />
  );
}

function SocialLink({ icon, label, link, ...rest }) {
  return (
    <Box as="li" {...rest}>
      <HoverLink link={link} label={label}>
        <Box size="18px" as={icon} />
      </HoverLink>
    </Box>
  );
}

export function LeftBar() {
  return (
    <Box
      display={{ base: 'none', md: 'block' }}
      width="40px"
      position="fixed"
      bottom="0px"
      left="40px"
      right="auto"
      z-index="10"
      color="rgb(168, 178, 209)"
    >
      <Stack spacing="40px" as="ul" align="center" listStyleType="none">
        <SocialLink
          icon={FaGithub}
          label="github"
          link="https://github.com/estheragbaje"
        />
        <SocialLink
          icon={FaLinkedin}
          label="linkedin"
          link="https://www.linkedin.com/in/folasadeagbaje/"
        />
        <SocialLink
          icon={FaTwitter}
          label="twitter"
          link="https://twitter.com/_estheragbaje"
        />

        <SocialLink
          icon={FaGlobe}
          label="blog"
          link="https://dev.to/estheragbaje"
        />
        <Divider orientation="vertical" height="200px" />
      </Stack>
    </Box>
  );
}
